<script>
import FertilizationLineCreateEditDailog from "@/components/fertilization/line-create-edit-dialog.vue";

import { useFertilizationGridInit } from "@/composable/fertilization";
import { onMounted, provide, watch } from "vue";

export default {
    components: { FertilizationLineCreateEditDailog },
    setup() {
        const {
            store,
            fertilizationGrid,
            sortToggle,
            updateFertilization,
            gridDesc,
            createFetilizationRow,
            fertilizationGridDays,
            openEmbryoDetailDialog,
            closeEmbryoDetailDialog,
            fillDaysRow,
            createFertilizationLineDialog,
            editFetilizationRow,
            ferilizationLine,
            showFateInGrid,
            onGridCellClick,
            selectedEmbryo
        } = useFertilizationGridInit();

        provide("createFertilizationLineDialog", createFertilizationLineDialog);
        provide("ferilizationLine", ferilizationLine);

        watch(() => store.state.fertilization.fertilization, fillDaysRow);

        onMounted(() => {
            store.commit("fertilization/EMPTY_FERTILIZAION_LINE");
        });

        return {
            fertilizationGrid,
            sortToggle,
            gridDesc,
            updateFertilization,
            createFetilizationRow,
            fertilizationGridDays,
            openEmbryoDetailDialog,
            closeEmbryoDetailDialog,
            editFetilizationRow,
            showFateInGrid,
            onGridCellClick,
            selectedEmbryo
        };
    }
};
</script>

